<template>
  <div class="top-aside">
    <div class="icon"
         @click="miniVariant">
      <mrc_ess_svgIcon icon-class="service"
                       style="font-size: 30px" />
    </div>
    <div class="rightFun">
      <span class="mr-2"
            v-if="$store.state.topcompanylist.length>=1">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="#f2f2f2"
                   rounded
                   :elevation='0'
                   v-if="showSelectCompany"
                   min-width='150'
                   dark
                   v-bind="attrs"
                   v-on="on">
              <span style="justify-content:space-between;width:100%;display:flex;align-items:center">
                <span style="color:#393939">{{companyName}}</span>
                <v-icon class="material-icons"
                        style="color:#393939">arrow_drop_down</v-icon>
              </span>-
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item v-for="(item, index) in topcompanylist"
                         :key="index"
                         @click="savecompany(item)">
              <v-list-item-title>{{ item.org_level_name }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </span>
      <span class="mr-2"
            v-else>
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="#f2f2f2"
                   rounded
                   :elevation='0'
                   v-if="showSelectCompany"
                   min-width='150'
                   dark
                   v-bind="attrs"
                   v-on="on">
              <span style="justify-content:space-between;width:100%;display:flex;align-items:center">
                <span style="color:#393939">{{companyName}}</span>
                <v-icon class="material-icons"
                        style="color:#393939">arrow_drop_down</v-icon>
              </span>-
            </v-btn>
          </template>
        </v-menu>
      </span>
      <!-- 分割线------------------------------------langage -->
      <v-menu offset-y
              v-if="$store.state.currentCompanyLanguage.length>=1"
              transition="scale-transition">
        <template v-slot:activator="{ on ,attrs}">
          <v-btn color="#f2f2f2"
                 rounded
                 :elevation='0'
                 min-width='125'
                 dark
                 v-bind="attrs"
                 v-on="on">
            <mrc_ess_svgIcon icon-class="outline-language-24px"
                             style="font-size: 23px;" />
            <span style="font-size:15px;color:#393939;">{{lang_name}}</span>
            <v-icon style="color:#393939"
                    class="material-icons ml-2">arrow_drop_down</v-icon>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item v-for="item in langList"
                       :key='item.value'
                       @click="lang_change(item)">
            <v-list-item-title>{{item.text}}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-menu offset-y
              v-else
              transition="scale-transition">
        <template v-slot:activator="{ on ,attrs}">
          <v-btn color="#f2f2f2"
                 rounded
                 :elevation='0'
                 min-width='125'
                 dark
                 v-bind="attrs"
                 v-on="on">
            <mrc_ess_svgIcon icon-class="outline-language-24px"
                             style="font-size: 23px;" />
            <span style="font-size:15px;color:#393939;">{{lang_name}}</span>
          </v-btn>
        </template>
      </v-menu>
      <!-- s----------------- -->
      <mrc_ess_svgIcon class="mx-2"
                       icon-class="round-notifications-24px"
                       style="font-size: 24px" />
      <span class="mr-5">
        <v-menu offset-y
                transition="slide-x-reverse-transition"
                :close-on-content-click='false'
                nudge-bottom='12'>
          <template v-slot:activator="{ on }">
            <!-- <v-icon v-on="on">mdi-format-list-bulleted-type</v-icon> -->
            <v-btn color="#f2f2f2"
                   rounded
                   small
                   fab
                   :elevation='0'
                   dark
                   v-on="on">
              <span class="black--text">MK</span>
            </v-btn>
          </template>
          <!-- <v-list dense> -->
          <!-- <v-list-item @click="login_out"> -->
          <!-- <v-list-item-title>登出</v-list-item-title> -->
          <div class="logBox">
            <div class="logTop"
                 :style="{backgroundColor: $vuetify.theme.defaults.light.primary}">
            </div>
            <div class="logFooter">
              <span class="userhead">
                <img src=""
                     alt="">
              </span>
              <h2 style="margin-top:77px">name</h2>
              <span class="mt-3"
                    style="color:#c2c2c2">name</span>
              <span class="mt-3"
                    style="color:#c2c2c2">name</span>
              <v-btn color="primary"
                     rounded
                     class="mt-5"
                     :elevation='0'
                     min-width='230'
                     min-height='40'
                     dark>
                <span>个人设置</span>
              </v-btn>
              <v-spacer class="mt-7"
                        style="height:2px;width:335px;background-color:#ececec;margin-bottom:30px"></v-spacer>
              <span :style="{color: $vuetify.theme.defaults.light.primary}"
                    @click="login_out">登出</span>
            </div>
          </div>
          <!-- </v-list-item> -->
          <!-- </v-list> -->
        </v-menu>
      </span>
    </div>
  </div>
</template>

<script>
import Cookie from 'js-cookie';
import { getCurrentList } from '@/service/api/internationalization'
export default {
  components: {},
  props: {},
  data () {
    return {
      items: ['Foo', 'Bar', 'Fizz', 'Buzz'],
      company: '',  //公司高级参照
      companyName: '',
      showSelectCompany: false
    };
  },
  created () { },
  async mounted () {
    if (this.$store.state.userData.roles != '001' && this.$store.state.userData.roles != '003') {

      this.showSelectCompany = true
      if (this.$store.state.topcompanylist.length <= 0) {
        await this.$store.dispatch('set_topcompanylist')
      }
      if (this.$store.state.loginStatus) { //用户第一次登录进入才会触发，刷新不会触发
        this.companyName = this.$store.state.topcompanylist[0].org_level_name
        this.$store.dispatch('set_currentCompany', this.$store.state.topcompanylist[0].uuid || '')
      }
      else {
        const items = this.$store.state.topcompanylist.filter(item => item.uuid == this.$store.state.currentCompany)
        this.companyName = items[0].org_level_name
      }
    }
    if (this.langList.length <= 0) {
      this.$store.dispatch('setCurrentCompanyLanguageList')
    }
  },
  methods: {
    async lang_change (item) {
      this.$store.commit('set_lang', item)
    },
    miniVariant () {
      this.$store.commit('set_miniVariant', !this.$store.state.miniVariant)
    },
    login_out () {
      // this.$store.commit('set_token', '')
      // localStorage.clear()
      this.$router.push({
        name: 'login'
      })
    },
    savecompany (item) {
      // console.log(item);
      if (item.org_level_name != this.companyName) {
        this.companyName = item.org_level_name
        this.$store.dispatch('set_currentCompany', item.uuid || '')
        this.$store.commit('set_currentCompanyClientUuid', item.client_uuid || '')
        window.location.reload()
      }
    }
  },
  computed: {
    langList () {
      return this.$store.state.currentCompanyLanguage
    },
    lang_name () {
      return this.$store.getters.lang_name
    },
    topcompanylist () {
      return this.$store.state.topcompanylist
    },
  },
  watch: {},
};
</script>

<style scoped>
.logBox {
  width: 393px;
  height: 445px;
  background-color: #ffffff;
  border-radius: 10px;
  position: relative;
}
.logTop {
  height: 89px;
}
.logFooter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.userhead {
  display: inline-block;
  width: 123px;
  height: 123px;
  border-radius: 50%;
  background-color: #efefef;
  position: absolute;
  left: 136px;
  top: 30px;
}
.top-aside {
  height: 56px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icon {
  display: flex;
  margin-left: 10px;
  cursor: pointer;
}
.top-aside /deep/ .v-select {
  width: 140px;
  height: 35px;
  padding: 0 10px;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
  background-color: #f2f2f2;
}
.top-aside /deep/ .v-input {
  flex: initial;
}
.top-aside .rightFun {
  width: 500px;
  /* padding-left: 135px; */
  display: flex;
  /* overflow-y: auto; */
  justify-content: flex-end;
  height: 100%;
  align-items: center;
}
</style>
